<script>
// import commUpload from '@/components/commonUpload'
import commonLikes from "@/components/commonLikes"
import commonPl from "@/components/commonPl"
import makeInfo from './makeInfo'
import { Indicator } from 'mint-ui';
import html2canvas from 'html2canvas'
import canvas2image from 'canvas2image';
import { GetDateAndHourStr, GetDateDel, returnOnlyTime, makeRandomCount, getLocal, getPhone, UtilsMakeImg } from '@/utils'
import { returnImg, returnName, returnNoreapt } from "@/utils/makeJson.js"

export default {
  components: {
    // commUpload,
    makeInfo,
    commonLikes,
    commonPl
  },
  name: 'List',
  data() {
    return {
      useItemPl: false,
      plMini: false,
      popupVisible3: false,
      dzDiaFlag: false,
      ifPj: false,
      plDiaFlag: false,
      tempPlData: {},
      tempPlIndex: '',
      popupVisible2: false,
      popupVisible5: false,
      popupVisible6: false,
      bgColor: '灰色', //页面背景色
      pyqImgSrc: '', //自己的朋友圈图
      setTimeLine: 0, //
      ifPj: false, //是否拼接朋友圈
      showUp: true,
      dataURL: '', //生成截图的链接
      ifShowBtn: false, //是否显示按钮
      show: false,
      marginTopClass: '', //离顶部的高度
      nowTime: returnOnlyTime(), //现在的时间
      showDiaTip: true, //弹窗提示
      formInline: { //表单信息
        hasZtl: false, //有无返回栏
        isSystemAz: true, //操作系统，安卓/苹果
        nickName: 'ant', //昵称
        yourHeaderImg: '', //头像
        makeSelfPl: '', //存储自定义评论
        contentStyle: '链接', //朋友圈形式(内容、链接)
        realContent: '', //朋友圈内容
        plType: '重复', //评论的类型(重复、自定义)
        realImgs: ['https://an888.net/image/1988-01.jpg'], //朋友圈的图片们
        realImgsWid: '', //朋友圈的图片的宽
        realImgsHei: '', //朋友圈的图片的高
        smallImg: '', // 链接缩略图
        smallContent: '哈哈哈哈哈哈哈哈哈哈哈哈哈哈', // 链接内容
        sendTime: '', // 发送时间
        localtion: '', //地点
        sendTimeNoformat: '', // 发送时间(未格式化)
        makeLikeCount: 2, //点赞个数
        makeContentCount: '', //评论个数
        makeContent: '', //评论内容
      },
      whichPhone: '', //手机型号
      screenUrl: '', //截图
      baseImgSizeClass: '', //根据图片的长宽决定图片的class
      makeBadInp: '', //假装的一个输入框
      likeImg: [], //头像
      wordsAll: [
        // {
        //   img: 'https://an888.net/image/fixer.jpg',
        //   nickName: 'ant',
        //   word: '嘻嘻嘻',
        //   time: GetDateAndHourStr()
        // }
      ]
    };
  },
  props: {
    userData: {},
  },
  mounted() {
    // 获取手机型号
    this.whichPhone = getPhone()
    let getRoute = this.$route
    // 得到用户选择的类型,是拼接还是自己写
    this.ifPj = getRoute.query && getRoute.query.type === 'two' ? true : false
    // 显示修改内容组件
    this.popupVisible3 = true
  },
  methods: {
    mmmm(url) {
      console.log(url)
      this.formInline.yourHeaderImg = url
    },
    changePlContent(item, index) {
      // 单独的修改评论
      // 评论弹窗显示
      this.popupVisible6 = true
      this.tempPlData = item //临时保存评论
      this.tempPlIndex = index //临时保存index
    },
    btnChangePL() {
      // 修改本条评论
      this.popupVisible6 = false //本体弹窗消失
      this.plDiaFlag = true //评论组件显示
      // 告诉评论组件显示我传过去的
      this.useItemPl = true

    },
    btnClearPL() {
      // 删除本条评论
      this.wordsAll.splice(this.tempPlIndex, 1)
      this.popupVisible6 = false //本体弹窗消失
    },
    changeDzInfo() {
      // 修改点赞
      this.popupVisible5 = true //弹窗显示

    },
    btnChangedz() {
      // 修改点赞内容
      this.dzDiaFlag = true //点赞显示
      this.popupVisible5 = false //本体消失

    },
    btnClearDz() {
      // 清除点赞内容
      this.popupVisible5 = false //本体消失
      // 清除点赞组件的内容
      this.$refs.compoentsLike.clearForm()
      // 本页面的点赞清空
      this.likeImg = []
    },
    jumpChange() {
      // 跳去改变内容
      console.log('jumpChange')
      // 关闭自己
      this.popupVisible2 = false
      // 展示编辑页面
      this.popupVisible3 = true
    },
    delContent() {
      // 删除本条内容
      console.log('delContent')
      this.popupVisible2 = false
    },
    getAllPl(data) {
      // 得到组件的评论内容
      if (this.useItemPl) {
        // 修改某条单独的评论,单独替换
        this.wordsAll.splice(this.tempPlIndex, 1, data)
      } else {
        // 添加一条新的评论
        this.wordsAll.push(data)
      }
    },
    changeContent() {
      // 修改内容
      console.log('修改内容')
      this.popupVisible2 = true
    },
    closePl() {
      this.plMini = false
    },
    goDz() { //设置点赞
      console.log('设置点赞')
      let t = setTimeout(() => {
        this.plMini = false
        this.dzDiaFlag = true
      }, 100)
    },
    goPl() { //设置评论
      console.log('设置评论')
      // 不是修改某条评论，而是新增的
      this.useItemPl = false
      let t = setTimeout(() => {
        this.plMini = false
        this.plDiaFlag = true
      }, 100)
    },
    setPlZan() { //设置点赞评论
      this.plMini = true
    },

    moveTime(type) { //控制时间条位置
      if (type === 'left') {
        --this.setTimeLine
      } else {
        ++this.setTimeLine
      }
    },

    goOn() { //继续制作
      // 图片清空,显示原制作页面
      this.dataURL = ''
      // 操作按钮显示
      this.ifShowBtn = true
    },
    newBtn(type) {
      if (type === 'reload') {
        // 重新获取头像
      } else if (type === 'img') {
        // 生成短图
        $(".detail_wapper").addClass('detail_wapper_position')
        $(".wx_bottom_return_mes").removeClass('changeClass').removeClass('changeClass3')
        this.makeImg()

      } else if (type === 'longimg') {
        // 生成长图
        $(".detail_wapper").removeClass('detail_wapper_position')
        // 根据有没有评论内容动态修改css
        if (!this.formInline.makeContent && !this.formInline.makeContentCount > 0) {
          $(".wx_bottom_return_mes").addClass('changeClass3')
        } else {
          $(".wx_bottom_return_mes").addClass('changeClass')
        }
        this.makeImg()
      } else if (type === 'back') {
        // 显示首页
        this.$router.push('/')
        // this.$emit('showIndex')
      }
    },
    loadding() { //加载loading
      Indicator.open({
        text: '努力生成中...',
        spinnerType: 'fading-circle'
      });
    },
    makeImg() {
      this.loadding()
      // 操作按钮消失
      this.ifShowBtn = false
      let _this = this
      // 延时 生成截图
      let t = setTimeout(() => {
        _this.shotCb()
      }, 300)
    },
    getInfoData(userData) {
      console.log(userData)
      if (userData) {
        let tempData = JSON.parse(JSON.stringify(userData))
        // 内容加上换行
        tempData.realContent = this.huanHang(tempData.realContent)
        this.formInline = tempData

        if (this.ifPj) {
          // 是拼图的，设置内容图片

        } else {
          // 用户自创的朋友圈
          //只有一张，加上宽高判断
          if (this.formInline.realImgsWid) {
            this.baseimg(tempData.realImgsWid, tempData.realImgsHei) //根据图片的长宽来决定怎么显示图片
          }
        }
        // this.getContent() //获取评论内容
        // this.baseSomeSetMargin() //根据有无状态栏、操作系统，判断离顶部的高度
        // 新增根据有无返回栏判断离顶高度
        this.checkIfHasback()
      }

    },
    huanHang(text) { //朋友圈内容的文字有换行
      // return text
      return text.replace(/\n|\r\n/g, "<br/>")
    },
    checkIfHasback() {
      let baseData = this.formInline
      if (baseData.hasZtl) {
        // 有状态栏，判断选择的安卓还是苹果
        if (baseData.isSystemAz) {
          console.log('安卓')
        } else {
          console.log('苹果')
        }
      }
    },
    baseSomeSetMargin() { //根据有无状态栏、操作系统，判断离顶部的高度
      let baseData = this.formInline
      let setHig //margin高度
      var _that = this
      if (baseData.hasZtl === '有') { //有状态栏
        // 计算状态栏+返回栏的高度
        if (baseData.ztlSystem === '安卓') {
          setHig = 'az_all_height'
        } else {
          setHig = 'ios_all_height'
        }
      } else { //没状态栏,只计算返回栏的高度
        if (baseData.ztlSystem === '安卓') {
          setHig = 'az_fh_height'
        } else {
          setHig = 'ios_fh_height'
        }
      }
      _that.marginTopClass = setHig
    },
    makePic() { //生成图片
      this.ifShowBtn = !this.ifShowBtn
      // this.shotCb()
    },
    getContent() { //获取评论内容
      //总共要生成多少条评论
      let howCount
      // 设置的评论类型是重复还是自定义
      var plType = this.formInline.plType ? this.formInline.plType : '重复'
      // 自定义评论
      var realPl = this.formInline.makeSelfPl.split("\n")
      if (plType === '重复') {
        howCount = this.formInline.makeContentCount
      } else {
        howCount = realPl.length
      }
      let _that = this
      let getNick = returnName(howCount) //拿到对应数量的昵称
      let getImgHeader = returnImg(howCount, 0, 300) //拿到对应数量的头像
      let getTimeNum = returnNoreapt(howCount, 0, 30) //生成0-30的随机数(时间)
      let makeTempJson = []
      for (var i = 0; i < howCount; i++) {
        if (plType === '重复') { //评论内容是重复，直接渲染重复的话语
          makeTempJson.push({
            img: getImgHeader[i],
            nickName: getNick[i],
            word: _that.formInline.makeContent,
            time: GetDateDel(_that.formInline.sendTimeNoformat, getTimeNum[i])

          })
        } else { //自定义评论，分别渲染评论
          if (realPl.length > 0 && realPl[0] !== "") { //自定义评论不为空
            makeTempJson.push({
              img: getImgHeader[i],
              nickName: getNick[i],
              word: realPl[i],
              time: GetDateDel(_that.formInline.sendTimeNoformat, getTimeNum[i])

            })
          }

        }

      }
      this.wordsAll = makeTempJson
    },
    jumpUrl() {
      window.href = this.formInline.smallImgLink
    },
    closeDialog() { //遮罩提示 显示控制
      this.showDiaTip = false
      // 操作区显示
      // this.ifShowBtn = true

    },
    topBack() { //上方返回
      this.$router.push('/')
    },
    getLikesImg(data) {
      // 遍历展示点赞
      this.likeImg = data
    },
    baseimg(realImgsWid, realImgsHei) { //根据图片的长宽来决定怎么显示图片
      // img_con_wapper_weight 宽图
      // img_con_wapper_zf 正方图
      // img_con_wapper_long 长图
      // img_con_wapper_more 多张图
      let imgwid = realImgsWid
      let imghei = realImgsHei
      // 分割线
      // 长宽差距不到20的话，认为是正方形
      let makeCountBad
      let makeCount
      makeCountBad = imgwid - imghei
      makeCount = imgwid - imghei
      if (makeCountBad < 0) { makeCountBad = -makeCountBad }
      if (makeCount < 0) { makeCount = -makeCount }
      if (makeCountBad < 20 || makeCount < 20) {
        this.baseImgSizeClass = 'img_con_wapper_zf'
        return false
      }

      // 分割线 end
      if (imgwid > imghei) { //宽图
        this.baseImgSizeClass = 'img_con_wapper_weight'
      } else if (imgwid < imghei) { //长图
        this.baseImgSizeClass = 'img_con_wapper_long'
      } else {
        // 正方形
        this.baseImgSizeClass = 'img_con_wapper_zf'
      }
      setTimeout(() => {
        let w = $(".common_back_one").width()
        let h = $(".common_back_one").height()
        console.log(h)
        $(".img_div_one").css({
          'width': w,
          height: imghei / (imgwid / w)
        })
      }, 300)

      // console.log($(".common_back_one").height())
      // console.log($(".common_back_one"))

    },
    shotCb() {
      let p = UtilsMakeImg(html2canvas, 'cutScreen', this)
      p.then((canvas) => {
        let dataURL = canvas.toDataURL("image/png");
        this.dataURL = dataURL;
      })

    }
  },
  watch: {

    dataURL() {
      if (this.dataURL) {
        Indicator.close();
      }
    }
  }
};

</script>
